.wp-block-create-block-cta-dynamic-block {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;



  // Style our image container

  .cta-image-container {
    width: 70%;
    align-self: stretch;
    position: relative;
    // background as placeholder when no image present
    background: #e5e5e5;


    img {
      position: absolute;
      height: 100%;
      width: 100%;
      object-fit: cover;
      top: 0;
      left: 0;
    }
  }

  // Style our text and button container
  .cta-text-container {
    box-sizing: border-box;
    width: 50%;
    margin-left: -20%;
    margin-top: 50px;
    margin-bottom: 50px;
    z-index: 2;
    background: white;
    box-shadow: 4px 0px 20px rgb(0,0,0,0.15);
    border-radius: 16px;
    padding: 50px 32px;

    h2 {
      margin-top: 0;
    }

  }

  .wp-element-button {
    display: inline-block;
  }


  // Add styles for a text left variant based on classname

  &.variant-text-left {
    flex-direction: row-reverse;

    .cta-image-container {
      justify-content: flex-end;
    }

    .cta-text-container {
      margin-left: 0%;
      margin-right: -20%;

    }
  }



  // Add responsive styles to stack on mobile for both layout variants
  @media (max-width: 768px){

    &.variant-text-left, &.variant-text-right {
      flex-direction: column;

      .cta-image-container {
        width: 100%;
        aspect-ratio: 1.25;
      }

      .cta-text-container {
        width: 90%;
        margin-left: 0;
        margin-top: -50px;
      }
    }
  }
}